import { FormValidationType } from "./../../models/from-validation-message";
import { FormValidationMessageField } from "src/app/models/from-validation-message";
import { Validators } from "@angular/forms";
import { FormControl } from "@angular/forms";
import { FormGroup } from "@angular/forms";
import { FormHelperService } from "./../../services/form-helper.service";
import { EnvService } from "src/app/services/env.service";
import { AlertService } from "src/app/services/alert.service";
import { NavController, MenuController } from "@ionic/angular";
import { AuthService } from "./../../services/auth.service";
import { Component, OnInit } from "@angular/core";
import { tap, catchError } from "rxjs/operators";
import { of } from "rxjs";

@Component({
  selector: "app-forgot-password-phone",
  templateUrl: "./forgot-password-phone.page.html",
  styleUrls: ["./forgot-password-phone.page.scss"],
})
export class ForgotPasswordPhonePage implements OnInit {
  requesting: boolean = false;
  forgotPasswordPhoneForm = new FormGroup({
    phone: new FormControl(
      "",
      Validators.compose([Validators.required, Validators.pattern("09\\d{9}")])
    ),
  });
  validationMessages: FormValidationMessageField[] = [];

  submitDisabled = false;
  constructor(
    private authService: AuthService,
    private navCtrl: NavController,
    private alertService: AlertService,
    private menu: MenuController,
    private env: EnvService,
    private formHelper: FormHelperService
  ) {
    this.formHelper.addValidationMessage(
      this.validationMessages,
      "phone",
      FormValidationType.REQUIRED,
      "شماره موبایل الزامی است."
    );
    this.formHelper.addValidationMessage(
      this.validationMessages,
      "phone",
      FormValidationType.PATTERN,
      "فرمت شماره موبایل صحیح نیست."
    );
  }
  ngOnInit(): void {}
  request(form: FormGroup) {
    if (!this.formHelper.validateForm(this.validationMessages, form)) {
      return;
    }
    this.requesting = true;
    this.authService
      .requestVerificationCodeForPasswordRetrieval(form.value.phone)
      .pipe(
        tap((result) => {
          this.authService.tempPhoneNumber = form.value.phone;
        }),
        catchError((err) => {
          this.alertService.presentError(
            this.env.serverErrorMessage(err.error["error_code"])
          );
          return of(false);
        })
      )
      .subscribe((result) => {
        this.requesting = false;
        if(result)
        {
          this.alertService.presentSuccess("پیام اعتبار سنجی ارسال شد.");
          this.navCtrl.navigateForward(["/forgot-password-password",form.value.phone]);
        }
        
      });
  }
  ionViewWillEnter() {
    this.menu.enable(false);
  }
  ionViewWillLeave() {
    this.menu.enable(true);
  }
}
